.shop {

    padding-top: 200px;

    &__title {}

    &__breadcrumbs {
        display: flex;
        padding: 0;

        li {
            &:first-child {
                &::before {
                    display: none;
                }
            }

            &::before {
                content: '—';
                margin: 0 5px 0 10px;
            }
        }
    }

    &__categories {
        margin-top: 100px;
        display: flex;
        justify-content: center;
    }

    &__button {}

    &__shown {
        margin-top: 75px;
    }

    &__cards {}

    &__card {
        margin-top: 50px;
    }


    &__pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 50px;

        img {
            padding: 15px;
            cursor: pointer;
        }

        li {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            background: white;
            color: black;
            border: 1px solid #000;
            margin: 0 7px;
            position: relative;
            z-index: 10;
            cursor: pointer;
            transition: all .5s ease;

            &::before {
                content: '';
                width: 0;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                transition: all .5s ease;
                background: black;
                z-index: -1;
            }

            &:hover {
                color: white;
                transition: all .5s ease;
            }

            &:hover::before {
                width: 100%;
                transition: all .5s ease;
            }

            &:last-child {
                margin: 0;
            }
        }
    }
}


.pagination-prev {
    img {
        transform: rotate(180deg);
    }
}

.pagination-active {
    background: black !important;
    color: white !important;
}

.bread-active {}

@import '../adaptive/adaptive_shop';